/* src/pages/Products.css */
.products-page {
  padding: 2rem 0;
  min-height: 80vh;
  background-color: #e4f2ef; /* Light background color */
}

.page-title {
  text-align: center;
  margin-bottom: 2.5rem;
  font-size: 2rem;
  color: #42403f; /* Dark text color */
  position: relative;
  font-weight: 600;
}

.page-title:after {
  content: '';
  display: block;
  width: 60px;
  height: 4px;
  background: linear-gradient(to right, #ef694d, #388895); /* Using coral and teal */
  margin: 0.5rem auto;
  border-radius: 2px;
}

.page-subtitle {
  text-align: center;
  color: #388895; /* Teal color */
  font-size: 1.1rem;
  margin-top: -1.5rem;
  margin-bottom: 2rem;
}

.products-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  gap: 2rem;
  margin-top: 2rem;
}

.products-loading {
  text-align: center;
  padding: 3rem;
  color: #388895; /* Teal color */
  font-size: 1.2rem;
}

.products-error {
  text-align: center;
  padding: 2rem;
  background-color: #f8d7da;
  border: 1px solid #f5c6cb;
  border-radius: 8px;
  color: #721c24;
  margin: 2rem auto;
  max-width: 500px;
}

.products-error a {
  color: #388895; /* Teal color */
  text-decoration: none;
  font-weight: 500;
}

.products-error a:hover {
  color: #ef694d; /* Coral color on hover */
  text-decoration: underline;
}

.no-products {
  text-align: center;
  padding: 3rem;
  color: #388895; /* Teal color */
  font-size: 1.1rem;
  grid-column: 1 / -1;
}

/* Filter section styles */
.filter-section {
  background-color: white;
  padding: 1.5rem;
  border-radius: 12px;
  box-shadow: 0 4px 15px rgba(56, 136, 149, 0.1); /* Teal shadow */
  margin-bottom: 2rem;
}

/* Responsive design */
@media (max-width: 768px) {
  .products-grid {
    grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
    gap: 1.5rem;
  }
  
  .products-page {
    padding: 1rem 0;
  }
  
  .page-title {
    font-size: 1.75rem;
    margin-bottom: 2rem;
  }
  
  .filter-section {
    padding: 1rem;
    margin: 0 1rem 1.5rem 1rem;
  }
}

@media (max-width: 480px) {
  .products-grid {
    grid-template-columns: 1fr;
    gap: 1rem;
  }
  
  .page-title {
    font-size: 1.5rem;
  }
  
  .page-subtitle {
    font-size: 1rem;
  }
}

/* Additional utility classes using your color palette */
.text-coral {
  color: #ef694d;
}

.text-teal {
  color: #388895;
}

.text-light {
  color: #e4f2ef;
}

.text-dark {
  color: #42403f;
}

.text-mustard {
  color: #dddb76;
}

.bg-coral {
  background-color: #ef694d;
}

.bg-teal {
  background-color: #388895;
}

.bg-light {
  background-color: #e4f2ef;
}

.bg-mustard {
  background-color: #dddb76;
}