/* src/components/ProductCard.css */
.product-card {
  transition: all 0.3s ease;
  border: 1px solid #374151;
  background-color: #1f2937;
  color: white;
  border-radius: 0.75rem;
  overflow: hidden;
  box-shadow: 0 10px 15px -3px rgba(0, 0, 0, 0.1), 0 4px 6px -2px rgba(0, 0, 0, 0.05);
}

.product-card:hover {
  transform: translateY(-4px);
  box-shadow: 0 20px 25px -5px rgba(0, 0, 0, 0.1), 0 10px 10px -5px rgba(0, 0, 0, 0.04);
}

.product-image-container {
  height: 16rem;
  background-color: #374151;
  overflow: hidden;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
}

.product-image {
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: transform 0.3s ease;
}

.product-card:hover .product-image {
  transform: scale(1.05);
}

.product-content {
  padding: 1rem;
}

.product-title {
  font-family: 'Georgia', 'Times New Roman', serif;
  font-size: 1.25rem;
  color: #fbbf24;
  margin-bottom: 0.25rem;
  font-weight: 600;
}

.product-meta {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 0.5rem;
  font-size: 0.875rem;
}

.product-brand {
  color: #ddd6fe;
}

.product-condition {
  font-size: 0.75rem;
  font-weight: 600;
  padding: 0.25rem 0.5rem;
  border-radius: 9999px;
}

.condition-new {
  background-color: #065f46;
  color: #d1fae5;
}

.condition-used {
  background-color: #92400e;
  color: #fef3c7;
}

.product-price {
  font-size: 1.125rem;
  font-weight: bold;
  margin-bottom: 0.75rem;
  color: #fbbf24;
}

.product-actions {
  display: flex;
  gap: 0.5rem;
}

.add-to-cart-btn {
  flex: 1;
  background-color: #f59e0b;
  color: #1f2937;
  padding: 0.5rem 1rem;
  border-radius: 9999px;
  font-weight: 600;
  border: none;
  cursor: pointer;
  transition: all 0.3s ease;
}

.add-to-cart-btn:hover {
  background-color: #fbbf24;
  transform: translateY(-1px);
}

.wishlist-btn {
  padding: 0.5rem;
  background-color: #374151;
  color: white;
  border-radius: 9999px;
  border: none;
  cursor: pointer;
  transition: all 0.3s ease;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 2.5rem;
  height: 2.5rem;
}

.wishlist-btn:hover {
  background-color: #4b5563;
  transform: scale(1.1);
}

/* Responsive design */
@media (max-width: 640px) {
  .product-image-container {
    height: 12rem;
  }
  
  .product-title {
    font-size: 1.125rem;
  }
  
  .product-actions {
    flex-direction: column;
  }
  
  .wishlist-btn {
    width: 100%;
    margin-top: 0.25rem;
  }
}

/* Loading state animation */
.product-card.loading {
  animation: pulse 2s infinite;
}

@keyframes pulse {
  0% {
    opacity: 0.7;
  }
  50% {
    opacity: 0.4;
  }
  100% {
    opacity: 0.7;
  }
}

/* Badge styles for new/sale items */
.product-badge {
  position: absolute;
  top: 0.5rem;
  right: 0.5rem;
  padding: 0.25rem 0.5rem;
  border-radius: 0.25rem;
  font-size: 0.75rem;
  font-weight: bold;
  z-index: 10;
}

.badge-new {
  background-color: #10b981;
  color: white;
}

.badge-sale {
  background-color: #ef4444;
  color: white;
}