/* Signup.css - Gen Z Fashion Theme */
.signup-container {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: calc(100vh - 100px);
  padding: 2rem;
  background-color: #f8f9fa;
  position: relative;
  overflow: hidden;
}

.signup-polka-dots {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-image: radial-gradient(#3498db 1.5px, transparent 1.5px),
                    radial-gradient(#ff6b6b 1px, transparent 1px);
  background-size: 60px 60px;
  background-position: 0 0, 30px 30px;
  opacity: 0.1;
  z-index: 0;
}

.signup-form {
  background: white;
  padding: 2.5rem;
  border-radius: 16px;
  box-shadow: 0 10px 25px rgba(0, 0, 0, 0.1);
  width: 100%;
  max-width: 450px;
  position: relative;
  z-index: 1;
  border: 1px solid #e9ecef;
}

.signup-header {
  text-align: center;
  margin-bottom: 2rem;
}

.signup-form h2 {
  margin-bottom: 0.5rem;
  color: #2c3e50;
  font-size: 1.8rem;
  font-weight: 700;
  letter-spacing: -0.5px;
}

.signup-subtitle {
  color: #7f8c8d;
  font-size: 1rem;
  margin-bottom: 0;
}

.form-group {
  margin-bottom: 1.5rem;
}

.form-group label {
  display: block;
  margin-bottom: 0.5rem;
  font-weight: 600;
  color: #2c3e50;
  font-size: 0.9rem;
}

.form-group input {
  width: 100%;
  padding: 0.875rem;
  border: 2px solid #e9ecef;
  border-radius: 8px;
  font-size: 1rem;
  box-sizing: border-box;
  transition: all 0.2s ease;
}

.form-group input:focus {
  outline: none;
  border-color: #3498db;
  box-shadow: 0 0 0 3px rgba(52, 152, 219, 0.1);
}

.password-input-container {
  position: relative;
}

.password-toggle {
  position: absolute;
  right: 12px;
  top: 50%;
  transform: translateY(-50%);
  color: #3498db;
  cursor: pointer;
  font-size: 0.85rem;
  font-weight: 600;
  background: white;
  padding: 0 0.5rem;
}

.signup-button {
  width: 100%;
  padding: 1rem;
  background: linear-gradient(135deg, #ff6b6b 0%, #3498db 100%);
  color: white;
  border: none;
  border-radius: 8px;
  font-size: 1rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  margin-bottom: 1.5rem;
  box-shadow: 0 4px 6px rgba(52, 152, 219, 0.2);
}

.signup-button:hover {
  transform: translateY(-2px);
  box-shadow: 0 7px 14px rgba(52, 152, 219, 0.3);
}

.signup-button:disabled {
  background: #bdc3c7;
  transform: none;
  box-shadow: none;
  cursor: not-allowed;
}

.signup-link {
  text-align: center;
  margin-top: 1.5rem;
}

.signup-link p {
  color: #7f8c8d;
  font-size: 0.95rem;
}

.signup-link a {
  color: #3498db;
  text-decoration: none;
  font-weight: 600;
  position: relative;
}

.signup-link a:after {
  content: '';
  position: absolute;
  bottom: -2px;
  left: 0;
  width: 100%;
  height: 2px;
  background: linear-gradient(90deg, #ff6b6b 0%, #3498db 100%);
  transform: scaleX(0);
  transform-origin: right;
  transition: transform 0.3s ease;
}

.signup-link a:hover:after {
  transform: scaleX(1);
  transform-origin: left;
}

.signup-message {
  padding: 0.875rem;
  border-radius: 8px;
  margin-bottom: 1.5rem;
  text-align: center;
  font-size: 0.9rem;
  font-weight: 500;
}

.signup-message.error {
  background-color: #ffebee;
  color: #c62828;
  border: 1px solid #ffcdd2;
}

@media (max-width: 480px) {
  .signup-container {
    padding: 1rem;
  }
  
  .signup-form {
    padding: 2rem 1.5rem;
    border-radius: 12px;
  }
  
  .signup-form h2 {
    font-size: 1.5rem;
  }
}