/* Navbar.css */
.navbar {
  background-color: #1f2937; /* slate-dark */
  color: white;
  padding: 0.75rem 2rem;
  position: sticky;
  top: 0;
  z-index: 1000;
}

.navbar-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.navbar-logo {
  font-size: 1.5rem;
  font-weight: bold;
  color: #facc15; /* goldAccent */
  text-decoration: none;
}

.navbar-links {
  list-style: none;
  display: flex;
  gap: 1.5rem;
}

.navbar-links a {
  text-decoration: none;
  color: #e5e7eb;
  transition: color 0.2s;
}

.navbar-links a:hover {
  color: #facc15; /* goldAccent hover */
}

.navbar-right {
  display: flex;
  align-items: center;
  gap: 1rem;
}

.navbar-cart {
  position: relative;
  font-size: 1.25rem;
  text-decoration: none;
  color: white;
}

.cart-count {
  background: #facc15; /* goldAccent */
  color: #1f2937;
  font-size: 0.75rem;
  font-weight: bold;
  padding: 0.25rem 0.5rem;
  border-radius: 999px;
  position: absolute;
  top: -8px;
  right: -12px;
}

.navbar-user {
  font-size: 0.9rem;
}

.navbar-btn {
  background: #facc15;
  color: #1f2937;
  border: none;
  padding: 0.5rem 1rem;
  border-radius: 999px;
  font-weight: bold;
  cursor: pointer;
  transition: transform 0.2s;
}

.navbar-btn:hover {
  transform: scale(1.05);
}

.payment-status {
  margin-left: 12px;
  padding: 4px 10px;
  border-radius: 12px;
  font-size: 0.85rem;
  font-weight: 500;
}

.payment-status.success {
  background-color: #d4f8d4;
  color: #2b7a2b;
}

.payment-status.failed {
  background-color: #ffd6d6;
  color: #b30000;
}

.payment-status.pending {
  background-color: #fff2cc;
  color: #996600;
}
