/* -------------------- Global Reset -------------------- */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Poppins', sans-serif;
  background-color: #f8f8f8;
  color: var(--text-color);
  line-height: 1.6;
}

/* -------------------- Theme Variables -------------------- */
:root {
  --primary-color: #ef694d;
  --secondary-color: #dddb76;
  --light-color: #e4f2ef;
  --dark-color: #388895;
  --text-color: #42403f;
  --border-radius: 8px;
  --transition: all 0.3s ease;
}

/* -------------------- App Layout -------------------- */
.App {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}

.main-content {
  flex: 1;
  padding: 20px;
}

/* -------------------- Buttons -------------------- */
.btn-primary {
  background-color: var(--primary-color);
  color: #fff;
  border: none;
  padding: 10px 20px;
  border-radius: var(--border-radius);
  cursor: pointer;
  font-weight: bold;
  transition: var(--transition);
}

.btn-primary:hover {
  background-color: #e05a3f;
  transform: translateY(-2px);
}

.btn-secondary {
  background-color: var(--secondary-color);
  color: var(--text-color);
  border: none;
  padding: 10px 20px;
  border-radius: var(--border-radius);
  cursor: pointer;
  font-weight: bold;
  transition: var(--transition);
}

.btn-secondary:hover {
  background-color: #d2d064;
  transform: translateY(-2px);
}

.btn-outline {
  background: transparent;
  border: 2px solid var(--dark-color);
  color: var(--dark-color);
  padding: 10px 20px;
  border-radius: var(--border-radius);
  font-weight: bold;
  cursor: pointer;
  transition: var(--transition);
}

.btn-outline:hover {
  background: var(--dark-color);
  color: #fff;
}

/* -------------------- Hero Section -------------------- */
.hero {
  background: linear-gradient(rgba(56, 136, 149, 0.7), rgba(56, 136, 149, 0.7)),
    url("https://images.unsplash.com/photo-1490481651871-ab68de25d43d?auto=format&fit=crop&w=1500&q=80")
      no-repeat center center/cover;
  color: #fff;
  text-align: center;
  padding: 80px 20px;
}

.hero h1 {
  font-size: 3rem;
  margin-bottom: 20px;
}

.hero p {
  font-size: 1.2rem;
  margin-bottom: 30px;
}

/* -------------------- Featured Collection -------------------- */
.home-container {
  padding: 40px 20px;
  max-width: 1200px;
  margin: auto;
}

.home-container h2 {
  text-align: center;
  font-size: 2rem;
  margin-bottom: 30px;
  color: var(--primary-color);
}

.featured-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 20px;
}

.featured-card {
  background: #fff;
  border: 1px solid #ddd;
  border-radius: var(--border-radius);
  overflow: hidden;
  transition: var(--transition);
  display: flex;
  flex-direction: column;
}

.featured-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 8px 18px rgba(0, 0, 0, 0.1);
}

.featured-image img {
  width: 100%;
  height: 200px;
  object-fit: cover;
}

.featured-info {
  padding: 15px;
  text-align: left;
}

.featured-info h3 {
  font-size: 1.2rem;
  color: var(--primary-color);
  margin-bottom: 5px;
}

.featured-meta {
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 0.9rem;
  margin-bottom: 10px;
}

.featured-meta p {
  color: var(--dark-color);
}

.condition {
  font-size: 0.8rem;
  font-weight: bold;
  padding: 3px 8px;
  border-radius: 20px;
}

.condition.new {
  background: #c6f6d5;
  color: #276749;
}

.condition.used {
  background: #fefcbf;
  color: #744210;
}

.price {
  font-size: 1.1rem;
  font-weight: bold;
  margin-bottom: 15px;
}

.btn-view {
  display: block;
  text-align: center;
  padding: 10px;
  border-radius: var(--border-radius);
  background: var(--secondary-color);
  color: var(--text-color);
  font-weight: bold;
  text-decoration: none;
  transition: var(--transition);
}

.btn-view:hover {
  background: #c9c753;
}

.view-all {
  margin-top: 30px;
  text-align: center;
}

.btn-view-all {
  text-decoration: none;
  padding: 12px 25px;
  background: var(--primary-color);
  color: #fff;
  border-radius: var(--border-radius);
  font-weight: bold;
  transition: var(--transition);
}

.btn-view-all:hover {
  background: #e05a3f;
}

/* -------------------- 404 Page -------------------- */
.not-found {
  text-align: center;
  padding: 80px 20px;
}

.not-found h1 {
  font-size: 4rem;
  color: var(--primary-color);
  margin-bottom: 10px;
}

.not-found p {
  font-size: 1.2rem;
  color: var(--dark-color);
  margin-bottom: 20px;
}

.btn-home {
  text-decoration: none;
  padding: 12px 25px;
  background: var(--secondary-color);
  color: var(--text-color);
  border-radius: var(--border-radius);
  font-weight: bold;
  transition: var(--transition);
}

.btn-home:hover {
  background: #c9c753;
}
