.product-detail {
  padding: 2rem 0;
  min-height: 70vh;
}

.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 1rem;
}

/* Breadcrumb */
.breadcrumb {
  display: flex;
  align-items: center;
  margin-bottom: 2rem;
  font-size: 0.9rem;
  color: #666;
}

.breadcrumb a {
  color: var(--dark-color);
  text-decoration: none;
  transition: color 0.3s ease;
}

.breadcrumb a:hover {
  color: var(--primary-color);
}

.breadcrumb-separator {
  margin: 0 0.5rem;
}

.breadcrumb-current {
  color: var(--text-color);
  font-weight: 500;
}

/* Loading State */
.product-detail-loading {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 4rem 2rem;
  text-align: center;
}

.loading-spinner {
  width: 40px;
  height: 40px;
  border: 3px solid #f3f3f3;
  border-top: 3px solid var(--primary-color);
  border-radius: 50%;
  animation: spin 1s linear infinite;
  margin-bottom: 1rem;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

/* Error State */
.product-not-found {
  text-align: center;
  padding: 4rem 2rem;
}

.error-icon {
  font-size: 3rem;
  margin-bottom: 1rem;
}

.product-not-found h2 {
  color: var(--text-color);
  margin-bottom: 1rem;
}

.product-not-found p {
  color: #666;
  margin-bottom: 2rem;
}

/* Product Detail Content */
.product-detail-content {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 3rem;
  margin-bottom: 3rem;
}

/* Product Gallery */
.product-gallery {
  display: flex;
  flex-direction: column;
}

.main-image {
  position: relative;
  width: 100%;
  height: 500px;
  overflow: hidden;
  border-radius: 12px;
  margin-bottom: 1rem;
  background: #f8f8f8;
}

.main-image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: transform 0.3s ease;
}

.main-image:hover img {
  transform: scale(1.02);
}

.thrifted-badge {
  position: absolute;
  top: 1rem;
  left: 1rem;
  background: var(--secondary-color);
  color: var(--text-color);
  padding: 0.5rem 1rem;
  border-radius: 20px;
  font-size: 0.8rem;
  font-weight: 600;
}

.image-thumbnails {
  display: flex;
  gap: 0.75rem;
}

.thumbnail {
  width: 80px;
  height: 80px;
  border-radius: 8px;
  overflow: hidden;
  cursor: pointer;
  border: 2px solid transparent;
  transition: all 0.3s ease;
  background: #f8f8f8;
}

.thumbnail.active {
  border-color: var(--primary-color);
}

.thumbnail:hover {
  border-color: var(--primary-color);
  transform: translateY(-2px);
}

.thumbnail img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

/* Product Info */
.product-info {
  padding: 1rem 0;
}

.product-header h1 {
  font-size: 2.5rem;
  color: var(--text-color);
  margin-bottom: 0.5rem;
  font-weight: 700;
}

.product-meta {
  display: flex;
  gap: 1rem;
  margin-bottom: 1.5rem;
}

.brand, .category {
  color: #666;
  font-size: 0.9rem;
}

.product-condition {
  display: flex;
  align-items: center;
  gap: 1rem;
  margin-bottom: 1.5rem;
}

.condition-badge {
  padding: 0.4rem 1rem;
  border-radius: 20px;
  font-size: 0.8rem;
  font-weight: 600;
}

.condition-badge.new {
  background: #e6f7ee;
  color: #0c9770;
}

.condition-badge.used {
  background: #fef6e7;
  color: #e9a309;
}

.stock-info {
  color: #666;
  font-size: 0.9rem;
}

.product-price {
  margin-bottom: 1.5rem;
}

.price {
  font-size: 2.2rem;
  font-weight: 700;
  color: var(--primary-color);
}

.product-description {
  margin-bottom: 2rem;
  line-height: 1.6;
  color: #555;
}

/* Size Selection */
.size-selection {
  margin-bottom: 2rem;
}

.size-selection h3 {
  margin-bottom: 1rem;
  color: var(--text-color);
  font-size: 1.1rem;
}

.size-options {
  display: flex;
  flex-wrap: wrap;
  gap: 0.75rem;
}

.size-btn {
  padding: 0.75rem 1.25rem;
  border: 2px solid #e0e0e0;
  background: white;
  border-radius: 8px;
  cursor: pointer;
  font-weight: 600;
  transition: all 0.3s ease;
  min-width: 60px;
}

.size-btn:hover {
  border-color: var(--primary-color);
}

.size-btn.selected {
  background: var(--primary-color);
  color: white;
  border-color: var(--primary-color);
}

/* Quantity Selection */
.quantity-selection {
  margin-bottom: 2rem;
}

.quantity-selection h3 {
  margin-bottom: 1rem;
  color: var(--text-color);
  font-size: 1.1rem;
}

.quantity-controls {
  display: flex;
  align-items: center;
  gap: 1rem;
}

.quantity-btn {
  width: 40px;
  height: 40px;
  border: 2px solid #e0e0e0;
  background: white;
  border-radius: 8px;
  font-size: 1.2rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
}

.quantity-btn:hover:not(:disabled) {
  border-color: var(--primary-color);
}

.quantity-btn:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

.quantity-display {
  font-size: 1.2rem;
  font-weight: 600;
  min-width: 40px;
  text-align: center;
}

/* Action Buttons */
.action-buttons {
  display: flex;
  gap: 1rem;
  margin-bottom: 2rem;
}

.btn-add-to-cart, .btn-buy-now {
  flex: 1;
  padding: 1rem 2rem;
  border: none;
  border-radius: 8px;
  font-size: 1rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0.5rem;
}

.btn-add-to-cart {
  background: var(--primary-color);
  color: white;
}

.btn-add-to-cart:hover:not(:disabled) {
  background: #e05a3f;
  transform: translateY(-2px);
}

.btn-buy-now {
  background: var(--dark-color);
  color: white;
}

.btn-buy-now:hover:not(:disabled) {
  background: #2a6b77;
  transform: translateY(-2px);
}

.btn-add-to-cart:disabled, .btn-buy-now:disabled {
  opacity: 0.6;
  cursor: not-allowed;
  transform: none;
}

.btn-icon {
  font-size: 1.2rem;
}

/* Out of Stock */
.out-of-stock-message {
  background: #fff3f3;
  border: 1px solid #ffcdd2;
  border-radius: 8px;
  padding: 1rem;
  margin-bottom: 2rem;
  text-align: center;
}

.out-of-stock-message p {
  color: #d32f2f;
  margin: 0;
  font-weight: 500;
}

/* Product Features */
.product-features {
  border-top: 1px solid #eee;
  padding-top: 2rem;
}

.product-features h3 {
  margin-bottom: 1rem;
  color: var(--text-color);
}

.product-features ul {
  list-style: none;
  padding: 0;
}

.product-features li {
  padding: 0.5rem 0;
  color: #555;
}

/* Responsive Design */
@media (max-width: 968px) {
  .product-detail-content {
    grid-template-columns: 1fr;
    gap: 2rem;
  }
  
  .main-image {
    height: 400px;
  }
}

@media (max-width: 768px) {
  .product-detail {
    padding: 1rem 0;
  }
  
  .product-header h1 {
    font-size: 2rem;
  }
  
  .price {
    font-size: 1.8rem;
  }
  
  .main-image {
    height: 350px;
  }
  
  .thumbnail {
    width: 70px;
    height: 70px;
  }
  
  .action-buttons {
    flex-direction: column;
  }
  
  .size-options {
    justify-content: center;
  }
}

@media (max-width: 480px) {
  .main-image {
    height: 300px;
  }
  
  .thumbnail {
    width: 60px;
    height: 60px;
  }
  
  .size-btn {
    padding: 0.6rem 1rem;
    min-width: 50px;
  }
  
  .btn-add-to-cart, .btn-buy-now {
    padding: 0.875rem 1.5rem;
  }
}